<template>
  <div class="bottomCenter list_box common_flex">
    <div style="width: 50%; height: 100%;text-align:center"><div id="bottomCenter"></div></div>
    <div  style="width: 50%; height: 100%;text-align:center"><div id="bottomCenter2"></div></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      leftData: {
          id: "bottomCenter",
          title: "制造中心能耗对比(单位能耗)",
          data: [
            { value: 1048, name: "Rosh车间" },
            { value: 735, name: "小单科(水表车间)" },
            { value: 580, name: "成西车间" },
            { value: 484, name: "成品半成品车间" },
            { value: 380, name: "颗粒车间" },
            { value: 684, name: "注塑车间" },
          ],
        },
        rightData: {
          id: "bottomCenter2",
          title: "行政中心能耗对比(人均能耗)",
          data: [
            { value: 248, name: "超市" },
            { value: 435, name: "宿舍" },
            { value: 580, name: "食堂" },
            { value: 1484, name: "充电桩" },
            { value: 680, name: "办公楼" },
          ],
        },
      option: {
        
      },
    };
  },
  methods: {
    createData(id, title, data) {
      var chartDom = document.getElementById(id);
      var myChart = this.$echarts.init(chartDom);
      var option;

      option = {
        title: {
          text: title,
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          bottom: "15",
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            type: "pie",
            radius: ["22%", "44%"],
            data: data,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            label: {
              show: true,
              position: "outside",
              color: "#fff",
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData(this.leftData.id,this.leftData.title,this.leftData.data);
    this.createData(this.rightData.id,this.rightData.title,this.rightData.data);
  },
};
</script>

<style lang="less" scoped>
#bottomCenter {
  width: 100%;
  height: 100%;
}
#bottomCenter2 {
  width: 100%;
  height: 100%;
}
.bottomCenter {
  width: 44%;
  padding-top: 20px;
}
</style>
